.mainBlock
  align-items: center
  display: flex
  flex-direction: column
  justify-content: center
  position: relative

.container
  align-items: center
  display: flex
  height: 99vh
  justify-content: center
  text-align: center
  width: 100vw

.speedingWheel
  -moz-animation: my_spinner 800ms infinite linear
  -webkit-animation: my_spinner 800ms infinite linear
  animation: my_spinner 800ms infinite linear
  border: 4px solid white
  border-left-color: transparent
  border-radius: 50%
  border-right-color: transparent
  height: 60px
  margin: 0 auto
  width: 60px

@keyframes my_spinner
  0%
    transform: rotate(0deg) scale(1)

  50%
    transform: rotate(180deg) scale(1.3)

  100%
    transform: rotate(360deg) scale(1)

